/* eslint-disable import/no-unresolved */

import PressQRCodeWeb from '../common/vue3/q-r-code';











export default {
  name: 'PressQRCode',
  components: {

    PressQRCodeWeb,

  },
  props: {
    value: { // url
      type: String,
      default: '',
      required: true,
    },
    size: { // px
      type: Number,
      default: 287,
      required: false,
    },
    margin: {
      type: Number,
      default: 0,
    },
  },
  emits: [
    'result',
  ],
  data() {
    return {
      codeImg: '',
      wxCanvasId: 'pressWxQRcode',
      canvasId: 'pressQRcode',
    };
  },
  computed: {
    style() {
      return `width: ${this.size}px; height: ${this.size}px;`;
    },
  },
  watch: {
    value: {
      handler() {
        this.codeImg = '';
        setTimeout(() => {
          this.genQRCode();
        });
      },
    },
  },
  mounted() {
    this.genQRCode();
  },
  methods: {
    genQRCode() {







    },
    codeMpWx() {
      const query = this.createSelectorQuery();

      query.select(`#${this.wxCanvasId}`)
        .fields({
          node: true,
          size: true,
        })
        .exec((res) => {
          const canvas = res[0].node;
          drawWxQRcode({
            canvas,
            canvasId: this.wxCanvasId,
            width: this.size,
            background: '#ffffff',
            foreground: '#000000',
            text: this.value,
          });

          wx.canvasToTempFilePath({
            canvasId: this.wxCanvasId,
            canvas,
            x: 0,
            y: 0,
            width: this.size,
            height: this.size,
            destWidth: this.size,
            destHeight: this.size,
            success: (res) => {
              console.log('[]', res.tempFilePath);
              this.codeImg = res.tempFilePath;

              this.$emit('result', this.codeImg);
            },
            fail: (res) => {
              console.error(res);
            },
          });
        });
    },
    codeMpQQ() {
      // uQRCode
      const qr = new UQRCode();
      // 
      qr.data = this.value;
      // canvas
      qr.size = this.size;
      qr.margin = Math.round(this.size / 20);
      // 
      qr.make();
      // canvas
      const canvasContext = uni.createCanvasContext(this.canvasId, this); // this
      // uQRCodecanvas
      qr.canvasContext = canvasContext;
      // canvas
      qr.drawCanvas();

      setTimeout(() => {
        uni.canvasToTempFilePath(
          {
            canvasId: this.canvasId,
            fileType: 'png',
            width: this.size,
            height: this.size,
            success: (res) => {
              console.log('[codeMpQQ]', res);
              this.codeImg = res.tempFilePath;

              this.$emit('result', this.codeImg);
            },
            fail: (err) => {
              console.log(err);
            },
          },
          this, // 
        );
      }, 300);
    },
    onLongPressImage() {
      this.$emit('longPressImage', this.codeImg);
    },
  },
};
